import axios from 'axios';
import React, { useRef, useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import ReactToPrint from 'react-to-print-advanced';
import WaterApiList from '../../../../Components/ApiList/WaterApiList';
import ComponentToPrint from './WaterUserPayReceipt';


function WaterUserPayReceiptIndex() {

    const componentRef = useRef();
    const [paymentData, setPaymentData] = useState();

    const { transactionId } = useParams()
    console.log("param payment id ..", transactionId)

    const { api_waterConsumerPaymentReceipt, header } = WaterApiList();

    useEffect(() => {
        fetchReceiptData()
    }, [])

    const fetchReceiptData = () => {
        axios.post(api_waterConsumerPaymentReceipt, { 'transactionNo': transactionId }, header)
            .then((res) => {
                if (res.data.status) {
                    console.log("Consumer Payment Data for receipt", res.data.data)
                    setPaymentData(res.data.data)
                } else {
                    console.log("Error while getting consumer data for receipt")
                }
            })
            .catch((err) => {
                console.log("Exception while getting consumer receipt", err)
            })
    }




    return (
        <div>
            {/* <NonBlockingLoader show={show} /> */}
            <ReactToPrint
                trigger={() => <button></button>}
                content={() => componentRef.current}
            />
            <ComponentToPrint ref={componentRef} paymentData={paymentData} />
        </div>
    )
}

export default WaterUserPayReceiptIndex